import axios from "axios";
import { Order, OrdersResponse  } from "./types";

// Create Axios instance without Authorization header initially
const api = axios.create({
  baseURL: "http://localhost:4000/orders", // backend orders route
  headers: {
    "Content-Type": "application/json",
  },
});

// Helper to set Authorization header dynamically
const getAuthHeaders = () => {
  if (typeof window !== "undefined") {
    //const token = localStorage.getItem("token");
    const token = localStorage.getItem("token");
    return { Authorization: token ? `Bearer ${token}` : "" };
  }
  return {};
};

// GET /orders
// export const getOrders = () =>
//   api.get<OrdersResponse[]>("", { headers: getAuthHeaders() });
export const getOrders = (page = 1, pageSize = 10) => {
  return api.get<OrdersResponse>("", {
    params: { page, pageSize },
  });
};

// POST /orders
export const createOrder = (data: Partial<Order>) =>
  api.post("", data, { headers: getAuthHeaders() });

// PUT /orders/:id
export const updateOrder = (id: string, data: Partial<Order>) =>
  api.put(`/${id}`, data, { headers: getAuthHeaders() });

// DELETE /orders/:id
export const deleteOrder = (id: number) =>
  api.delete(`/${id}`, { headers: getAuthHeaders() });
